@use 'base/variables' as v;
@use 'base/mixins' as m;

.table{
    width: 100%;
    border-collapse: collapse;

    &__thead{
        background-color: v.$gris-oscuro;
        color: v.$blanco;
    }
    &__th{
        padding: 1rem;
        text-align: left;
        &--dash{
            padding: 1rem;
            text-align: center;
            @include m.tablet {
                text-align: center;
            }
        }
    }
    &__tbody{

    }
    &__tr{
        &:nth-child(even){
            background-color: v.$gris-claro;
        }
        &--dash{
            border-radius: 1.8rem;
            &:nth-child(even){
            background-color: rgba(51, 51, 51, 0.1); 
        }
        }
    }
    &__td{
        padding: 1.5rem 1rem;

        &--acciones{
            @extend .table__td;
            display: flex;
            justify-content: flex-end;
            gap: 2rem;
            align-items: center;
        }
        &--dash{
            text-align: center;
        }

    }
    &__accion{
        all: unset;
        text-transform: uppercase;
        font-size: 1.4rem;
        font-weight: v.$bold;

        &--editar{
            color: v.$azul;
        }
        &--eliminar{
            color: v.$rojo;
        }
        &:hover{
            cursor: pointer;
        }
    }

    &__formulario{

    }
}